import React from 'react'
import Dropdown from 'Component/Dropdown'
import Decimal from 'Component/Input/Decimal'
import InputText from 'Component/Input/text'

const WeightEntry = ({
    entryListIdx,
    wmsPoSkuInfoId,
    gradeList,
    ripeness,
    inputWidth,
    inboundTypeList,
    showTitle,
    item,
    onChange,
    onChangeDropdown,
    uom,
    poListIdx
}) => {
    let gradeDisplay = uom === 'kg' || uom === 'gram' ? true : false

    return (
        <div className='w-100 we-m'>
            <div className='d-flex w-100 we-m'>
                <Dropdown
                    isEmpty={item.inboundTypeIsEmpty}
                    ddType={'inboundType'}
                    selectedValue={item?.inboundType}
                    entryListIdx={entryListIdx}
                    wmsPoSkuInfoId={wmsPoSkuInfoId}
                    uom={'kg'}
                    onChangeDropdown={(selected) => onChangeDropdown({ selected, name: 'inboundType' })}
                    title='Inbound Type'
                    placeholder='Inboud Type'
                    options={inboundTypeList}
                    width='drp-w-50'
                    showTitle={showTitle}
                    show={true}
                />
                <Dropdown
                    isEmpty={item.gradeIsEmpty}
                    selectedValue={item?.grade}
                    poListIdx={poListIdx}
                    entryListIdx={entryListIdx}
                    wmsPoSkuInfoId={wmsPoSkuInfoId}
                    uom={uom}
                    show={gradeDisplay}
                    onChangeDropdown={(selected) => onChangeDropdown({ selected, name: 'grade' })}
                    title='Order Id'
                    placeholder='Select Order Id'
                    options={gradeList}
                    width='drp-w-50'
                    showTitle={showTitle}
                    className={ripeness ? 'd-none' : null} />
                <Decimal
                    isEmpty={item.qtyIsEmpty}
                    entryListIdx={entryListIdx}
                    wmsPoSkuInfoId={wmsPoSkuInfoId}
                    uom={uom}
                    ripeness={ripeness}
                    inputWidth={inputWidth}
                    // values={item?.qty}
                    title={'Input Weight'}
                    showTitle={showTitle}
                    onChange={(e) => onChange(e)} />
            </div>
            <div className='d-flex w-50'>
                <InputText
                    isEmpty={item.reasonIsEmpty}
                    entryListIdx={entryListIdx}
                    wmsPoSkuInfoId={wmsPoSkuInfoId}
                    inputWidth={'w-30'}
                    values={item?.reason}
                    showTitle={showTitle}
                    title={'Note'}
                    onChange={(e) => onChange(e)} />
            </div>
        </div>
    )
}
export default WeightEntry