import { CREATE_JOB_SHEET } from 'apiServices/action'



export const addRow = ({
    dispatch,
    moduleEntry,
    moduleName,
    activeSku,
    poListIdx,
}) => {
    let js = { ...moduleEntry }
    const grade = js.jobPost.list[activeSku].gradeList?.filter(grade => { return grade?.value === js.jobPost.list[activeSku].skuUuid })
    const itemList = js.jobPost.list[activeSku].poList[poListIdx].itemList
    const lastSelected = itemList[itemList?.length - 1]
    const gradeSelected = lastSelected ? lastSelected.grade : grade[0]
    js.jobPost.list[activeSku].poList[poListIdx].itemList.push({
        qty: '',
        grade: gradeSelected,
        inboundType: { label: '', value: '' },
        ripeness: null
    })
    dispatch({ type: CREATE_JOB_SHEET, [`${moduleName}EntryData`]: js })
}

export const updateJSWeightLine = ({
    dispatch,
    moduleEntry,
    moduleName,
    activeSku,
    poListIdx,
    entryListIdx,
    e
}) => {
    const name = e?.target?.name
    let value = e?.target?.value
    let js = { ...moduleEntry }

    if (name === 'weight') {
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].qty = parseInt(value)
        let regex = /^[0-9\.\x0]+$/
        console.clear()

        if ((regex.test(value) === false && value !== "")) {
            return;
        }
        let newValue = 0;
        let arr = value != 0 ? value.split('.') : 0
        let number = arr[0];
        if (number > 0) number = parseInt(number)
        let commaNumber = arr[1];

        if ((commaNumber !== null && commaNumber !== undefined)) {
            if (commaNumber.length > 2) {
                //jika dibelakang koma ada lebih dari 2 angka
                var sliceNumber = commaNumber.slice(0, 2);
                newValue = `${number}.${sliceNumber}`
            } else {
                newValue = `${number}.${commaNumber}`
            }
        } else {
            newValue = parseInt(number)
        }
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].qty = newValue
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].qtyIsEmpty = false
    }

    else if (name !== 'note') {
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].qty = value > 0 ? parseInt(value) : value
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].qtyIsEmpty = true
    }
    if (name === 'note') {
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].note = value
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].noteIsEmpty = true
    }
    const initialWeight = js.jobPost.list[activeSku].poList[poListIdx].inboundQty
    let initialWeightUpdate = initialWeight
    js.jobPost.list[activeSku].poList[poListIdx].edited = false
    js.jobPost.list[activeSku].poList[poListIdx].minus = false
    js.jobPost.list[activeSku].poList[poListIdx].itemList.map(data => {
        if (data.qty) js.jobPost.list[activeSku].poList[poListIdx].edited = true
        initialWeightUpdate = initialWeightUpdate - (!isNaN(data.qty) ? data.qty : 0)
        js.jobPost.list[activeSku].poList[poListIdx].initialWeightUpdate = initialWeightUpdate
        if (initialWeightUpdate < 0) {
            js.jobPost.list[activeSku].poList[poListIdx].minus = false
            const value = js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].qty
            if (name === 'qty') {
                js.jobPost.list[activeSku].poList[poListIdx].initialWeightUpdate = parseInt(initialWeightUpdate) + parseInt(value ? value : 0)
            }
            if (name === 'weight') {
                js.jobPost.list[activeSku].poList[poListIdx].initialWeightUpdate = parseFloat(initialWeightUpdate) + parseFloat(value ? value : 0)
            }
            js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].qty = ''
            if (js.jobPost.list[activeSku].poList[poListIdx].initialWeightUpdate < 0) js.jobPost.list[activeSku].poList[poListIdx].initialWeightUpdate = initialWeight
        }
    })

    dispatch({ type: CREATE_JOB_SHEET, [`${moduleName}EntryData`]: js })
}

export const updateJSWeightLineDropdown = ({
    dispatch,
    moduleEntry,
    moduleName,
    activeSku,
    poListIdx,
    entryListIdx,
    selected,
    name
}) => {
    let js = { ...moduleEntry }
    if (name === 'grade') {
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].grade = selected
        js.jobPost.list[activeSku].poList[poListIdx].edited = true
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].gradeIsEmpty = false
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].ripeness = { label: null, value: null }
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].ripenessIsEmpty = false
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].note = null
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].noteIsEmpty = false
    }
    if (name === 'ripeness') {
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].ripeness = selected
        js.jobPost.list[activeSku].poList[poListIdx].edited = true
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].ripenessIsEmpty = false
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].note = null
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].noteIsEmpty = false
    }
    if (name === 'inboundType') {
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].inboundType = selected
        js.jobPost.list[activeSku].poList[poListIdx].edited = true
        js.jobPost.list[activeSku].poList[poListIdx].itemList[entryListIdx].inboundTypeIsEmpty = false
    }
    dispatch({ type: CREATE_JOB_SHEET, [`${moduleName}EntryData`]: js })
}


