import React, { useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import WeightEntryCard from 'Page/StockEntry/AdditionalInbound/WeightEntry/WeightEntryList/WeightEntryCard'
import { removeEntryModule, saveJobSheet } from 'apiServices'
import { jobSheetChecker, totalWeightCount } from 'Page/StockEntry/GeneralServices/moduleServices'
import { submitValidation } from 'Page/StockEntry/StockTake/services'
import { ModalsConfirmation as Confirmation } from 'Component/Modal/ModalConfirmation'
import PopUpSaved from "Component/Modal/PopUpSaved";
import PopUpRequired from "Component/Modal/PopUpRequired";
import Discard from 'Component/Modal/PopUpDiscard'
import Sort from 'Component/Sort'

const WeightEntryList = ({
    setActiveSKU,
    hideSKUList,
    setHideSKUList,
    activeSku,
    moduleEntry,
    moduleName,
    updateEntry,
    setUpdateEntry
}) => {
    const dispatch = useDispatch()
    const status = useSelector(state => state.status)
    const isLoading = useSelector(state => state.isLoading)
    const { skuName, skuNumber, poList, gradeList, unitOfMeassures } = moduleEntry.jobPost.list[activeSku]
    const totalWeight = totalWeightCount({ moduleEntry, activeSku })
    const edited = jobSheetChecker({ moduleEntry })
    const [isSuccess, setIsSuccess] = useState({ success: false, name: null })
    const [confirmation, setConfirmation] = useState({ show: false, name: null })
    const [discard, setDiscard] = useState(false)
    const [isSaved, setIsSaved] = useState()
    const [action, setAction] = useState()
    const [isRequired, setIsRequired] = useState(false)

    useEffect(() => {
        if (isSuccess.status === 500) {
            setIsSaved(false)
            setConfirmation({ show: false, name: confirmation.name })
        }
        else if (isSuccess.status === 200) {
            setConfirmation({ show: false, name: confirmation.name })
            setIsSaved(true)
        }

    }, [isSuccess])
    useEffect(() => {
        if (!isSaved) setIsSuccess({ success: false, name: null })
    }, [isSaved])
    const postInfo = isSuccess.success && isSuccess.status ? true : false

    return (
        <div className='w-100 entryList-m entryList-mobile pl-3'>

            <Discard
                discard={discard}
                module={'Inventory Entry - Stock Take'}
                moduleEntry={moduleEntry}
                close={() => setDiscard(false)}
                discardEntry={(id) => removeEntryModule({ id, moduleName, dispatch })}
            />
            <PopUpSaved
                updateEntry={updateEntry}
                setUpdateEntry={setUpdateEntry}
                status={status}
                moduleEntry={moduleEntry}
                submit={() => dispatch({ type: 'CREATE_JOB_SHEET', [`${moduleName}EntryData`]: null, [`${moduleName}SummaryData`]: null })}
                confirmation={confirmation}
                setConfirmation={setConfirmation}
                isSaved={isSaved}
                action={action}
                setIsSaved={setIsSaved}
                moduleName={moduleName}
                totalSku={moduleEntry?.jobPost?.list.length}

            />
            <PopUpRequired
                isRequired={isRequired}
                module={moduleEntry}
                setIsRequired={setIsRequired}
                moduleName={moduleName}
                totalSku={moduleEntry?.jobPost?.list.length}

            />
            <Confirmation
                isLoading={isLoading}
                moduleEntry={moduleEntry}
                confirmation={confirmation}
                setConfirmation={setConfirmation}
                isSuccess={postInfo}
                setAction={setAction}
                action={action}
                save={() => saveJobSheet({ name: 'save', moduleEntry, moduleName, dispatch, setIsSuccess })}
                submit={() => saveJobSheet({ name: 'submit', moduleEntry, moduleName, dispatch, setIsSuccess, submitValidation, setActiveSKU })}
            />
            <div className='mb-3 bg-white rounded-bottom rounded-top'>
                <div className='w-100 h-screen-title mr-0 d-flex align-items-center border-top border-left border-right border-bottom rounded-top border-soft-green p-3'>
                    <div className='d-flex w-75'>
                        <div className={`${hideSKUList ? 'iconU-sideBarHideRight' : 'iconU-sideBarHideLeft'} emptyTextField fontSizeTitle mr-3`} onClick={() => setHideSKUList(!hideSKUList)} />
                        <div className='mr-3 pr-1 d-flex align-items-center m-0 p-0 LatoBold'>Weight Entry</div>
                        <div className='mr-3 iconU-sideBarDot d-flex align-items-center' />
                        <div className='mr-3 d-flex align-items-center font-green LatoBold'>{`${skuName} (${skuNumber})`}</div>
                    </div>
                    <Sort moduleEntry={moduleEntry} moduleName={moduleName} activeSku={activeSku} dispatch={dispatch} />
                </div>
                <div className='w-100 h-screen-content-right mr-0 border-left border-right border-soft-green pl-3 pr-1 pt-3'>
                    {
                        poList?.map((poItem, idx) => {
                            return <WeightEntryCard
                                // inboundTypeList={inboundTypeList}
                                gradeList={gradeList}
                                inputWidth='input-w-100'
                                activeSku={activeSku}
                                poListIdx={idx}
                                poItem={poItem}
                                moduleEntry={moduleEntry}
                                moduleName={moduleName}
                            />
                        })
                    }

                </div>
                <div className='w-100 d-flex align-items-center p-3 border-left border-right border-bottom rounded-bottom LatoBold'>
                    <button
                        type="button"
                        className={`btn ${edited ? 'bg-green' : 'bg-lineGrey'}  text-white px-5 mr-3 LatoBold`}
                        onClick={() => edited ? setConfirmation({ show: true, name: 'save' }) : null}>
                        Save
                    </button>
                    <div>Total Weight <span className='font-green'>{`${totalWeight} ${unitOfMeassures?.toUpperCase()}${totalWeight > 1 ? "s" : ''}`}</span></div>
                </div>
            </div>
            <div className="d-flex align-items-center justify-content-end">
                <div onClick={() => setDiscard(true)} className='red pr-3 fontSizeButton cursor-pointer'><i className='iconU-entryTrash'></i> Discard</div>
                <button onClick={() => edited ? submitValidation({ setActiveSKU, moduleEntry, moduleName, dispatch, setConfirmation, setIsRequired }) : null} className={`btn px-5 py-2 text-white ${edited ? 'bg-green' : 'bg-lineGrey'} LatoBold no-shadow`} style={{ height: "fit-content !important" }} >Submit</button>
            </div>
        </div>
    )
}

export default WeightEntryList