import React, { useState } from 'react'
import { Redirect } from 'react-router-dom'
import { useSelector } from 'react-redux'
import SelectedSKUList from 'Page/StockEntry/AdditionalInbound/WeightEntry/SelectedSKUList'
import WeightEntryList from 'Page/StockEntry/AdditionalInbound/WeightEntry/WeightEntryList'

const WeightEntry = () => {
    const inboundEntryData = useSelector(state => state.inboundEntryData)
    const [hideSKUList, setHideSKUList] = useState(false)
    const [activeSku, setActiveSKU] = useState(0)
    const moduleName = 'inbound'
    if (!inboundEntryData || inboundEntryData?.jobPost.list.length === 0) {
        return (
            <div className='d-flex align-items-center justify-content-center text-center w-100 h-screen'><Redirect to='/inventory-entry/additional-inbound'>Please Select At Least 1 SKU</Redirect></div >)
    }
    return (

        <div className='w-100'>
            <div className='w-100 d-md-flex justify-content-between scroll-y z-0'>
                <SelectedSKUList
                    hideSKUList={hideSKUList}
                    activeSku={activeSku}
                    setActiveSKU={setActiveSKU}
                    moduleName={moduleName}
                    moduleEntry={inboundEntryData}
                />
                <WeightEntryList
                    setHideSKUList={setHideSKUList}
                    hideSKUList={hideSKUList}
                    moduleName={moduleName}
                    moduleEntry={inboundEntryData}
                    activeSku={activeSku}
                />
            </div>
        </div>
    )
}

export default WeightEntry