import React from 'react'
import { Link } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import { ADD_SKU } from 'apiServices/action'
import './index.scss'
import SelectedSKU from 'Page/StockEntry/AdditionalInbound/WeightEntry/SelectedSKUList/SelectedSKU'
const SelectedSKUList = ({
    hideSKUList,
    setActiveSKU,
    activeSku,
    moduleEntry,
    moduleName,
    setUpdateEntry,
}) => {
    const selectedSku = moduleEntry.jobPost.list.length

    const dispatch = useDispatch()
    return (
        <div className={`${hideSKUList ? 'w-0' : 'w-30'}  mt-md-0 rounded-bottom rounded-top skuList-m`}>
            <div className='w-100 bg-white h-screen-title p-3 border-top border-left border-right rounded-top border-soft-green text d-flex align-items-center justify-content-between'>
                <div className="LatoBold"> Selected SKU <span className='font-green'>{`(${selectedSku} SKU${selectedSku > 1 ? 's' : ''})`}</span></div>

                <div className='d-flex align-items-center justify-content-between'>
                    <Link to='/inventory-entry/additional-inbound/weight-entry/weight-entry/remove-sku'>
                        <div className="fontSizeTitle px-3 emptyTextField iconU-remove" />
                    </Link>
                    <Link to='/inventory-entry/additional-inbound/weight-entry/weight-entry/add-sku'>
                        <div onClick={() => dispatch({ type: ADD_SKU, [`${moduleName}AddSku`]: true })} className='fontSizeTitle emptyTextField text-bold iconU-entryAdd' ></div>
                    </Link>
                </div>
            </div>
            <div className='w-100 bg-white h-screen-content mr-0 border rounded-bottom border-soft-green pl-3 pr-1 pt-3'>
                {
                    moduleEntry?.jobPost.list.map((data, idx) =>
                        <SelectedSKU
                            data={data}
                            idx={idx}
                            activeSku={activeSku}
                            skuDescription={data?.skuName}
                            skuNumber={data?.skuNumber}
                            uomLabel={data?.unitOfMeassures}
                            uomValue={data?.totalQty}
                            uom={data?.unitOfMeassures}
                            img={data?.photo}
                            onClick={() => setActiveSKU(idx)}
                        />)
                }
            </div>
        </div>
    )
}

export default SelectedSKUList