import React from 'react'

const text = ({ placeholder, inputWidth, values, showTitle, title, isEmpty, onChange, poListIdx, entryListIdx }) => {
    return (
        <div className={` ${inputWidth} mb-3 mr-3`}>
            <div className={` pb-1 LatoBold ${showTitle ? null : 'd-none'}`}>{title}</div>
            <div className={`${isEmpty ? 'border-red' : 'border'} rounded border-soft-green w-100 bg-white d-flex align-items-center input-height px-2 mr-3`}>
                <input id={`noteob_${poListIdx}_${entryListIdx}`} autoComplete='off' type='text' className='w-100 plain-input' value={values} name='reason' placeholder={placeholder} onChange={(e) => onChange(e)} />
            </div>
        </div>
    )
}

export default text