import React from "react";
import { isFloat, dateFull } from 'services'
import Trash from "assets/icons/trash.png";
import WeightEntry from "Page/StockEntry/AdditionalInbound/WeightEntry/WeightEntryList/WeightEntryCard/WeightEntry";
import "./index.scss";
import {
  addRow,
  updateJSWeightLine,
  updateJSWeightLineDropdown,
} from "Page/StockEntry/AdditionalInbound/WeightEntry/services";
import { countQty, deleteRow } from 'Page/StockEntry/GeneralServices/moduleServices'
import { useDispatch } from "react-redux";

const WeightEntryCard = ({
  inputWidth,
  activeSku,
  poListIdx,
  poItem,
  moduleEntry,
  inboundTypeList,
  moduleName,
  gradeList
}) => {
  const dispatch = useDispatch();
  const {
    inboundDate,
    initialWeight,
    inboundQty,
    initialWeightUpdate,
    initialWeightScreenUpdate,
    supplierName,
    uom,
    ripeness,
    edited,
    isHidden
  } = poItem;
  const total = !isNaN(initialWeightUpdate) ? initialWeightUpdate : inboundQty;
  const initialWeightTotal = !isNaN(initialWeightScreenUpdate) ? initialWeightScreenUpdate : initialWeight
  const uomType = uom === "kg" || uom === "gram" ? "Weight" : "Quantity";

  return (
    <div className={`${isHidden ? 'd-none' : null} z-0 d-flex flex-column justify-content-between w-100 mb-3 align-items-center border rounded border-soft-green ${edited ? "soft-green" : null
      }`}
    >
      <div className="w-100 border-bottom border-soft-green my-3 d-flex align-items-start px-3">
        <div className="d-flex align-items-center LatoBold pr-3">{`${poListIdx + 1
          }.`}</div>
        <div className="w-100 d-flex justify-content-between align-items-center LatoBold cardheader-m">
          <div className="d-flex align-items-center cardheader-m">
            <div className="d-flex align-items-center mb-3 mr-5">
              {`Inbound Date`}
              <span className="ml-2 font-green">
                {` ${dateFull(inboundDate)}`}
              </span>
            </div>
            <div className="d-flex align-items-center mb-3 mr-3">
              {`Supplier Name`}
              <span className="ml-2 font-green">{` ${supplierName}`}</span>
            </div>
          </div>
        </div>
      </div>

      <div className="d-flex flex-column justify-content-between w-100 align-items-center px-3 z-0">
        {poItem.itemList?.map((item, entryListIdx) => {
          return (
            <div className="d-flex w-100 align-items-center justify-content-between">
              <div className="w-100 d-flex">
                <div style={{ width: '10px' }} className="input-height d-flex align-items-center mb-3 mr-3 pt-4 mt-2">{`${entryListIdx + 1}.`}</div>
                <div className="w-100">
                  <WeightEntry
                    entryListIdx={entryListIdx}
                    wmsPoSkuInfoId={item.wmsPoSkuInfoId}
                    showTitle={true}
                    gradeList={gradeList}
                    inboundTypeList={inboundTypeList}
                    onChange={(e) =>
                      updateJSWeightLine({
                        dispatch,
                        moduleEntry,
                        moduleName,
                        activeSku,
                        poListIdx,
                        entryListIdx,
                        e,
                      })
                    }
                    onChangeDropdown={({ selected, name }) =>
                      updateJSWeightLineDropdown({
                        dispatch,
                        moduleEntry,
                        moduleName,
                        activeSku,
                        poListIdx,
                        entryListIdx,
                        selected,
                        name,
                      })
                    }
                    uom={uom}
                    ripeness={ripeness}
                    inputWidth={inputWidth}
                    item={item}
                    className={!ripeness ? "d-none" : null}
                    poListIdx={poListIdx}
                  />
                </div>
              </div>
              <div className="d-flex w-10 align-items-center justify-content-end input-height mb-0 p-0">
                <img
                  src={Trash}
                  alt="delete icon"
                  className="w-1 cursor-pointer"
                  onClick={(e) =>
                    deleteRow({
                      dispatch,
                      moduleEntry,
                      moduleName,
                      activeSku,
                      poListIdx,
                      entryListIdx,
                      e,
                      countQty
                    })
                  }
                />
              </div>
            </div>
          );
        })}
      </div>
      <div
        // className={`w-100 ml-5 pl-4 font-green mb-3 LatoBold cursor-pointer ${total === 0 ? 'd-none' : null}`}
        className={`w-100 ml-5 pl-4 font-green mb-3 LatoBold cursor-pointer`}
        onClick={() => addRow({
          dispatch, moduleEntry,
          moduleName, activeSku, poListIdx
        })}
      >
        <i className="green iconU-entryAdd pr-2"></i>
        {`Add ${uomType} (${isFloat(initialWeightTotal) ? isFloat(initialWeightTotal) : initialWeightTotal} ${uom ? uom.toUpperCase() : ''}${initialWeightTotal > 1 ? "s" : ""} left)`}
      </div>
    </div>
  );
};
export default WeightEntryCard;
